<template>
  <div ref="calendar"></div>
</template>

<script>
import { Calendar } from '@fullcalendar/vue';
import dayGridPlugin from '@fullcalendar/daygrid';
import DataService from '../../service/DataService';

export default {
  name: 'Calendar',
  components: {
    Calendar
  },
  props: {
    eventData: {
      type: Array,
      default: () => []
    }
  },
  data() {
    return {
      calendar: null,
      initialized: false,
      events: [] // Define events array in data
    };
  },
  mounted() {
    this.renderCalendar();
  },
  methods: {
    renderCalendar() {
      const calendarEl = this.$refs.calendar;
      this.calendar = new Calendar(calendarEl, {
        plugins: [dayGridPlugin],
        initialView: 'dayGridMonth',
        events: this.eventData,
        headerToolbar: {
          start: 'prev',
          center: 'title',
          end: 'next'
        },
        datesSet: this.handleDatesSet
      });
      this.calendar.render();
    },
    handleDatesSet(info) {
      var vm=this;
      if (this.initialized) {
        //info.startStr, info.endStr
        //var startDate =info.
        debugger
        var startDt= vm.$options.filters.formatDate(info.startStr);
        var endDt= vm.$options.filters.formatDate(info.endStr);
        var url = `userId=0&&locationId=${this.$route.params.lid}&assetCatId=${this.$route.params.aid}&rid=${this.$route.params.rid}&start=${startDt}&end=${endDt}&requestedEventType=`;
    
        this.LoadAssetCalendar(url);
      } else {
        this.initialized = true;
        var url = `userId=0&&locationId=${this.$route.params.lid}&assetCatId=${this.$route.params.aid}&rid=${this.$route.params.rid}&start=&end=&requestedEventType=`;
        this.LoadAssetCalendar(url);
      }
    },
    LoadAssetCalendar(url) {
      DataService.LoadAssetCalendar(url).then(response => {
        this.events = []; // Clear existing events
        const AssetCalendarData = response.data;
        debugger
        AssetCalendarData.forEach(item => {
          // Add events to the events array
          this.events.push({
            title: item.title + '(' + item.quantity + ')',
            start: item.start,
            end: item.end,          
            classNames:  this.getEventBackgroudCssClass(item.title)
          });
        });
        // Refresh the calendar events
        this.calendar.removeAllEvents();
        this.calendar.addEventSource(this.events);
      });
    },
    getEventBackgroudCssClass(title) {
      // Define your logic to determine CSS class based on event title
      // Example logic:
      if (title === 'AVAILABLE') {
        return ['r-calendar-available'];
      } else if (title === 'RESERVED') {
        return ['r-calendar-reserved'];
      } else if (title === 'ASSIGNED') {
        return ['r-calendar-assigned'];
      } else if (title === 'IN-TRANSIT') {
        return ['r-calendar-intransit'];
      } else if (title === 'AUDIT (annual )' || title === 'CALIBRATION (quarterly )') {
        return ['r-calendar-audit'];
      } else {
        return ['r-calendar-light-purple'];
      }
    },
    getEventTextCssClass(title) {
      // Define your logic to determine CSS class based on event title
      // Example logic:
      if (title === 'AVAILABLE') {
        return ['white'];
      } else if (title === 'RESERVED') {
        return ['black'];
      } else if (title === 'ASSIGNED') {
        return ['white'];
      } else if (title === 'IN-TRANSIT') {
        return ['white'];
      } else if (title === 'AUDIT (annual )' || title === 'CALIBRATION (quarterly )') {
        return ['white'];
      } else {
        return ['white'];
      }
    }
  }
};
</script>
<style>
.r-calendar-available { background-color: #a6cdb2 !important; color: #fff !important; padding: 2px !important; text-transform: capitalize;}
.r-calendar-reserved {background-color: #7bb3ca !important; color: #fff !important; padding: 2px !important;text-transform: capitalize;}
.r-calendar-intransit {background-color: #E5C77E !important; color: #fff !important; padding: 2px !important; text-transform: capitalize;}
 .r-calendar-audit {background-color: #a5c63b !important; color: #fff !important; padding: 2px !important; text-transform: capitalize;}
.r-calendar-assigned {background-color: #e5c77e !important; color: #fff !important; padding: 2px !important;text-transform: capitalize;}
.r-calendar-grey {background-color: #737373 !important;color: #fff !important; padding: 2px !important;text-transform: capitalize;}
.r-calendar-brown {background-color: #6d3b24 !important;color: #fff !important; padding: 2px !important; text-transform: capitalize;}
.r-calendar-light-purple {background-color: #d4d1f0 !important; color: #fff !important; padding: 2px !important; text-transform: capitalize;}
</style>
